<template lang="pug">
    div.content-help
        div.help-list
            div.help-list-title 热门问题
            list-block.help-list-block
                list-item(:link="`/user/setting/help_detail?id=${item.id}&title=${item.question}`" :itemTitle="item.question" v-for="item in hot_faq" key="")
            div.help-list-title 全部问题
            div.help-list-block-all
                router-link.help-list-block-all-a(:to="`/user/setting/faq?faq_group=${item.group}`" v-for="item in faq_cates" key="")
                    span(v-text="item.group")
</template>
<script>
import listBlock from '@/components/listBlock'
import listItem from '@/components/listItem'
import { Toast } from 'mint-ui'
export default {
    name: 'settingHelp',
    components: {
        listBlock,
        listItem
    },
    data() {
        return {
            hot_faq: '',
            faq_cates: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/settingsHelp' : this.api.settingsHelp();
            this.axios.get(axiosUrl).then(res => {
                this.user = res.data.user;
                if (res.data.status == 1) {
                    this.hot_faq = res.data.hot_faq;
                    this.faq_cates = res.data.faq_cates;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
            .catch(err => {
                console.log(err);
                Toast(err || '请求失败');
            });
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.help-list {
    & .help-list-title {
        margin-top: .5rem;
        color: #333;
        padding-left: .75rem;
        font-size: 0.8rem;
    }
    & .help-list-block {
        margin: .5rem 0;
        min-height: 2.75rem;
    }
    & .help-list-block-all {
        width: 100%;
        background-color: #fff;
        margin-top: .5rem;
        &:after {
            display: block;
            content: "";
            clear: both;
        }
        & .help-list-block-all-a {
            position: relative;
            display: inline-block;
            width: 50%;
            height: 3rem;
            line-height: 3rem;
            text-align: center;
            float: left;
            border-bottom: .1rem solid #f0f0f0;
            &:nth-child(2n):after {
                display: block;
                content: "";
                width: .1rem;
                height: 100%;
                background-color: #f0f0f0;
                position: absolute;
                top: 0;
                left: -0.05rem;
            }
            &:nth-last-of-type(1) {
                border-bottom: none;
            }
            &:nth-last-of-type(2) {
                border-bottom: none;
            }
            & i {
                font-size: 1rem;
                margin-right: .3rem;
                color: @color-theme;
            }
            & span {
                color: #999;
            }
        }
    }
}
</style>



